import React from 'react';
import FavoriteSharpIcon from '@mui/icons-material/FavoriteSharp';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import MailIcon from '@mui/icons-material/Mail';
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import LanguageIcon from '@mui/icons-material/Language';
// import {useNavigate} from 'react-router-dom';

function Card({ data,deleter,hide }){
    let [like,setLike]=React.useState(false)
    
   function fetch(id){
    localStorage.setItem('id',id);
   }
    return(
        <div className='cardHolder'>
            <div className='imageSection'>
                <img src={data.image} alt=''/>
            </div>
            <div className='detailsSection'>
                <div>
                    <h2>{data.name}</h2>
                </div>
                <div>
                <MailIcon className='icons'/>
                <p>{data.email}</p>
                </div>
                <div>
                    <PhoneAndroidIcon className='icons'/>
                <p>{data.phone}</p>
                </div>
                <div>
                    <LanguageIcon className='icons'/>
                <a href="www.youtube.com">{data.website}</a>
                </div>
            </div>
            <div className='footerSection'>
                <ul>
                    <li onClick={()=>{setLike(!like)}}> 
                        {like ? <FavoriteSharpIcon className='like icon'/> : <FavoriteBorderIcon className='like icon'/>}
                    </li>
                    <li onClick={()=>(hide(false),fetch(data.id))}>
                        <EditIcon className='edit icon' />
                    </li>
                    <li onClick={()=>{deleter(data.id)}}> 
                        <DeleteIcon className='delete icon' />
                    </li>
                </ul>
            </div>
        </div>
    )
}

export default Card;