import React from 'react';
import Card from './Card';
import Content from './Context'

function Cardlist({show}){
    let [valcg,setcg]=React.useState(true);

    let val =React.useContext(Content)

    let viewVal=val;
    // console.log(val.useVal)
    // deleting the record
    
    function remove(id){
        delete val[id-1];
        setcg(!valcg)
    }
    React.useEffect(()=>{
    },[valcg]);
    return(
        <div className='cardListHolder'>
            {viewVal.map((item)=>{
                return(
                    <Card key={item.id} data={item} deleter={remove} hide={show}/>
                )
            })}
        </div>
    )
}

export default Cardlist;