import React from 'react';
import './App.css';
import CardList from './Components/Cardlist'
import Form from './Components/Form';
import {BrowserRouter,Routes,Route} from 'react-router-dom';
import axios from 'axios';
import Content from './Components/Context';

function App() {
  
  let[View,setView]=React.useState(true);
  
  // datas
  let [useVal,setUseval]=React.useState([]);

  async function getData(){
    let res= await axios.get('https://630887cd722029d9ddd15ac2.mockapi.io/restfulapi');
    setUseval(res.data);
  }

  React.useEffect(()=>{
      getData();
  },[]) 

function update(i){
  let temp=useVal.map((v)=>{
    if(v.id===i.id){
      return i
    }
    else{
      return v
    }
  })
  setUseval(temp);
}

  return (
    <Content.Provider value={useVal}>
    <div className='App'>
      <BrowserRouter>
      <Routes>
      <Route path='/' element={<CardList show={setView}/>}/> 
      </Routes>
      {View? "" :<Form view={setView} submitter={update}/>}
      </BrowserRouter>
    </div>
    </Content.Provider>
  );
}

export default App;
