import React from 'react';
import ClearIcon from '@mui/icons-material/Clear';
// import {useNavigate} from 'react-router-dom';4
import Content from './Context'

function Formedit({view,submitter}){
    let val =React.useContext(Content)

    let d_id = localStorage.getItem('id') 
    
    let [editVal,setEditval]=React.useState({
        id:"",
        name:"",
        email:"",
        phone:"",
        website:"",
        image:"",
        like:""
    })
    
    function valChange(){
        setEditval(()=>{
            return{
                id:d_id,
                ...val[d_id-1]
            }
        })
    }
    
    React.useEffect(()=>{
        valChange();
    },[])

    function changer(el){
        setEditval(()=>{
            return{
                ...editVal,
                [el.target.name]:el.target.value          
            }
        })
    }

    return(
        <div className='formPage'>
        <div className='formHolder'>
           <div className='form' >
            <ClearIcon className='close icon' onClick={()=>view(true)} />
            <span htmlFor="name"><span className='clrRed'>*</span>Name:</span>
            <input id="name" type="text" onChange={changer} name="name" value={editVal.name}/>
            <span htmlFor="email"><span className='clrRed'>*</span>Email:</span>
            <input id="email" type="text" onChange={changer} name="email" value={editVal.email}/>
            <span htmlFor="number"><span className='clrRed'>*</span>Phone:</span>
            <input id="number" type="text" onChange={changer} name="phone" value={editVal.phone}/>
            <span htmlFor="web"><span className='clrRed'>*</span>Web:</span>
            <input id="web" type="text" onChange={changer} name="website" value={editVal.website}/>
            <button onClick={()=>(submitter(editVal),view(true))} >Submit</button>
           </div>
        </div>
        </div>
    )
}

export default Formedit;